.footer {
	background: #313243;
	padding: 40px 0 44px 0;

	.container {
		@include sm-block {
			max-width: 320px;
			margin-left: auto;
			margin-right: auto;
		}
	}

	&-top {

		&__left, &__center, &__right {
			@include sm-block {
				width: 100%;
			}
		}

		&__row {
			display: flex;
			@include sm-block {
				flex-direction: column;
				align-items: center;
			}
		}

		&__col {
			&-title {
				font-weight: 500;
				font-size: 18px;
				line-height: 28px;
				color: #FFFFFF;
				margin: 0 0 24px 0;
				@include sm-block {
					margin-bottom: 16px;
				}
			}
		}

		&__left {
			flex: 0 0 358px;
			margin-right: 20px;
			@include md-block {
				flex: 0 0 280px;
			}
			@include sm-block {
				flex: unset;
				margin-right: 0;
				order: 3;

				.footer-top__logo-wrapper {
					display: none;
				}
			}
		}

		&__center {
			flex: 0 0 250px;
			@include sm-block {
				flex: unset;
				margin-bottom: 28px;
			}

			.footer-top__logo-wrapper {
				display: none;
				@include sm-block {
					display: block;
					margin-bottom: 18px;
				}
			}
		}

		&__right {
			margin-left: auto;
			@include sm-block {
				margin-left: 0;
				margin-bottom: 28px;
			}
		}

		&__socials {
			margin-top: 20px;
		}

		&__social {
			display: inline-block;
			transition: all 0.3s;
			margin-right: 12px;

			&:hover {
				opacity: 0.7;
			}
		}

		&__menu {
			padding: 0;
			margin: 0;
			list-style: none;

			&-item {

				&:not(:last-of-type) {
					margin-bottom: 8px;
				}
			}

			&-link {
				font-weight: 400;
				font-size: 16px;
				line-height: 24px;

				color: #FFFFFF;
				text-decoration: none;
			}

			&--small {
				.footer-top__menu-item {
					&:not(:last-of-type) {
						margin-bottom: 12px;
					}
				}

				.footer-top__menu-link {
					font-weight: 400;
					font-size: 14px;
					line-height: 20px;
					color: #EBEFF4;
				}
			}
		}

		&__right {
			display: flex;
			flex-direction: column;
		}

		&__email, &__phone {
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #FFFFFF;
			text-decoration: none;
			position: relative;
			padding-left: 32px;
		}

		&__email {
			margin-bottom: 12px;
		}

		&__email::before {
			content: '';
			display: block;
			position: absolute;
			width: 24px;
			height: 24px;
			background: url("../img/icon-email.svg") no-repeat center / contain;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
		}

		&__phone::before {
			content: '';
			display: block;
			position: absolute;
			width: 24px;
			height: 24px;
			background: url("../img/icon-phone.svg") no-repeat center / contain;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
		}
	}

	&-bottom {
		margin-top: 36px;

		@include sm-block {
			margin-top: 28px;
		}

		.container {
			padding-top: 20px;
			position: relative;
			@include sm-block {
				padding-top: 16px;
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				width: calc(100% - 60px);
				max-width: 100%;
				left: 50%;
				transform: translateX(-50%);
				height: 1px;
				background: #c9d0db;
				@include sm-block {
					width: calc(100% - 30px);
				}
			}
		}

		p {
			margin: 0;
			font-weight: 400;
			font-size: 14px;
			line-height: 20px;
			color: #C9D0DB;


		}
	}
}