.slider {
	position: relative;

	.swiper-wrapper {
		height: auto;
	}

	&__arrow {
		width: 44px;
		height: 44px;
		border-radius: 50%;
		border: none;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 3;
		cursor: pointer;
		transition: opacity 0.3s;

		&:hover {
			opacity: 0.8;
		}

		&:focus {
			outline: none;
		}

		&.swiper-button-disabled {
			opacity: 0;
		}

		&--left {
			left: 0;
			background: url("../img/icon-arrow-left.svg") no-repeat center / 24px, #FFF;
		}

		&--right {
			right: 0;
			background: url("../img/icon-arrow-right.svg") no-repeat center / 24px, #FFF;
		}

	}

	&__slide {
		border-radius: 16px;
		overflow: hidden;
		border: 1px solid #C9D0DB;
		height: auto;
		display: flex;
		flex-direction: column;
		align-items: stretch;

		&-img-wrapper {
			display: inline-block;
		}

		&-img {
			display: block;
			width: 100%;
		}

		&-content {
			padding: 8px;
			flex-grow: 1;
			display: flex;
			flex-direction: column;
			align-items: stretch;

			.recommendations__bottom {
				margin-top: auto;
			}
		}

		&-title {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			text-decoration: none;
			margin-bottom: 8px;
			display: block;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}