.attraction-map {
	&__holder {
		border-radius: 12px;
		overflow: hidden;
		margin-bottom: 8px;
		height: 340px;
		background: #e6e6e6;
	}
	&__location {
		position: relative;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		padding-left: 32px;
		margin: 0 0 12px 0;

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 24px;
			height: 24px;
			background: url("../img/icon-location-light-blue.svg") no-repeat center / contain;
		}
	}
}