.calendar {
	border-radius: 12px;
	overflow: hidden;
	margin-bottom: 20px;

	&__head {
		padding: 16px 16px 12px 16px;
		background: #fff;
		border-radius: 12px 12px 0 0;
		position: relative;
		cursor: pointer;

		&.closed {
			&::after {
				transform: translateY(-50%) rotate(-180deg);
			}
		}

		&::after {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 8px;
			width: 24px;
			height: 24px;
			background: url("../img/arrow-up.svg") no-repeat center / contain;
			transition: all 0.3s;
		}
	}

	&__head-text {
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		color: #313243;
	}

	&__input {
		background: #EBEFF4;
		border-radius: 8px;
		padding: 8px;
		display: block;
		width: 100%;
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		color: #141414;
		margin-bottom: 12px;
		border: 1px solid transparent;
		transition: all 0.3s;

		&::placeholder {
			color: #757A82;
		}

		&:focus {
			outline: none;
			border-color: #141414;
		}
	}
	&__content {
		border-top: 1px solid #C9D0DB;
		background: #fff;
		padding: 12px;
		border-radius: 0 0 12px 12px;

		.air-datepicker.-inline- {
			margin-left: auto;
			margin-right: auto;
			border: none;
		}
	}

	&__button {
		width: 100%;
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		color: #313243;
		border: 1px solid #757A82;
		border-radius: 76px;
		margin-top: 12px;
		background: transparent;
		cursor: pointer;
		display: inline-block;
		padding: 10px 20px;
		transition: all 0.3s;

		&:hover {
			background: #313243;
			border-color: #313243;
			color: #ffffff;
		}
	}
}