.quote-block {
	padding-left: 20px;
	border-left: 1px solid #E20E17;
	max-width: 672px;
	margin-top: 20px;
	margin-bottom: 24px;
	position: relative;
	padding-bottom: 16px;
	@include sm-block {
		padding-left: 12px;
		padding-bottom: 4px;
	}

	&::after {
		content: '';
		display: block;
		position: absolute;
		width: 54px;
		height: 48px;
		background: url("../img/icon-quote.svg") no-repeat center / contain;
		pointer-events: none;
		right: 47px;
		bottom: -5px;
		@include sm-block {
			right: 0;
			bottom: 0;
			width: 41px;
			height: 37px;
		}
	}
	.quote-block {
		&__text {
			font-weight: 400;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 12px 0;

			@include sm-block {
				font-size: 18px;
				margin-bottom: 9px;
			}
		}

		&__author {
			display: flex;
			align-items: center;
			&-img {

				@include sm-block {
					display: none;
				}

			}
			&-name {
				font-weight: 400;
				font-size: 20px;
				line-height: 28px;
				color: #757A82;
				margin-left: 12px;

				@include sm-block {
					font-size: 16px;
					line-height: 24px;
					margin-left: 0;

					&::before {
						content: '— ';

					}
				}
			}

			&-info {
				display: flex;
				flex-direction: column;
			}

			&-position {
				margin-top: 4px;
				font-size: 16px;
				line-height: 24px;
				color: #757A82;
			}

			&--large {
				.quote-block__author-info {
					margin-left: 12px;
				}
				.quote-block__author-name {
					margin-left: 0;
				}
			}
		}
	}
}