.mob-bar {
	display: none;
	@include sm-block {
		position: fixed;
		z-index: 100;
		bottom: 0;
		width: 100%;
		background: #fff;
		left: 0;

		display: flex;
		justify-content: center;
	}
	&__button {
		width: 64px;
		text-align: center;
		text-decoration: none;
		padding: 7px 3px;
		display: flex;
		flex-direction: column;
		align-items: center;

		svg {

			&.active {
				display: none;
			}
			&.inactive {
				display: block;
			}
		}

		&-text {
			font-size: 12px;
			line-height: 18px;
			text-align: center;
			color: #757A82;
			@include xs-block {
				font-size: 10px;
				line-height: 16px;
			}
		}

		&.active {
			background: #3AA0DA;
			svg {
				&.active {
					display: block;
				}
				&.inactive {
					display: none;
				}
			}
			.mob-bar__button-text {
				color: #ffffff;
			}
		}
	}
}