.shop-block {
	border: 1px solid #C4C4C4;
	border-radius: 16px;
	margin: 24px 0;
	padding: 20px;
	max-width: 672px;
	@include sm-block {
		padding: 16px;

	}
	&__head {
		display: flex;
		align-items: center;
		margin-bottom: 12px;

		p {
			font-weight: 500;
			font-size: 24px;
			line-height: 28px;
			color: #313243;
			margin: 0;
			@include sm-block {
				font-size: 20px;
				line-height: 24px;
			}
		}
	}

	&__img {
			flex-shrink: 0;
		margin-right: 12px;
	}

	&__content {
		font-weight: 400;
		font-size: 20px;
		line-height: 28px;
		color: #313243;
		@include sm-block {
			font-size: 18px;
			line-height: 26px;
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		margin-top: 12px;
			margin-bottom: -10px;
		flex-wrap: wrap;
	}

	&__location, &__price {
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		margin-right: 16px;
		padding-left: 32px;
		position: relative;
		@include sm-block {
			margin-right: 12px;
			font-size: 14px;
			margin-bottom: 10px;
		}
		&::before {
			content: '';
			display: block;
			position: absolute;
			width: 24px;
			height: 24px;
			background: no-repeat center / contain;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
		}
	}

	&__location {
		&::before {
			background-image: url("../img/icon-location-blue.svg");
		}
	}
	&__price {
		&::before {
			background-image: url("../img/icon-price-blue.svg");
		}
	}
}