.city {
	border-radius: 12px;
	overflow: hidden;

	&__head {
		padding: 16px 16px 12px 16px;
		background: #fff;
		border-radius: 12px 12px 0 0;
		position: relative;
		cursor: pointer;

		&.closed {
			&::after {
				transform: translateY(-50%) rotate(-180deg);
			}
		}

		&::after {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: 8px;
			width: 24px;
			height: 24px;
			background: url("../img/arrow-up.svg") no-repeat center / contain;
			transition: all 0.3s;
		}
	}

	&__head-text {
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		color: #313243;
	}

	&__content {
		border-top: 1px solid #C9D0DB;
		background: #fff;
		padding: 12px;
		border-radius: 0 0 12px 12px;

	}

	&__radio {
		display: flex;
		align-items: flex-start;
		position: relative;
		cursor: pointer;

		&:not(:last-of-type) {
			margin-bottom: 12px;
		}

		input {
			position: absolute;
			left: 0;
			top: 0;
			opacity: 0;

			&:checked {
				+ .city__radio-indicator {
					border-color: #FFC036;
					&::before {
						opacity: 1;
					}
				}
			}
		}

		&-indicator {
			margin-right: 8px;
			flex-shrink: 0;
			width: 24px;
			height: 24px;
			border: 1px solid #C9D0DB;
			border-radius: 50%;
			position: relative;
			transition: all 0.3s;

			&::before {
				content: "";
				display: block;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: 12px;
				height: 12px;
				border-radius: 50%;
				background: #FFC036;
				opacity: 0;
				pointer-events: none;
				transition: all 0.3s;
			}

		}
		&-label {
			font-size: 16px;
			line-height: 24px;
			color: #313243;
		}
	}

}