.article-accordion {
	border: 1px solid #C9D0DB;
	border-radius: 16px;
	margin-bottom: 24px;
	overflow: hidden;
	@include sm-block {
		border: none;
		border-radius: 0;
	}

	&.closed {
		.article-accordion__head {
			&::after {
				transform: translateY(-50%) rotate(-180deg);
			}
		}
	}
	.article-accordion {

		&__head {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			text-decoration-line: underline;
			color: #313243;
			transition: opacity 0.3s;
			background: #EBEFF4;
			padding: 20px 60px 20px 20px;
			overflow: hidden;
			position: relative;
			cursor: pointer;
			@include sm-block {
				font-size: 18px;
			}

			@include sm-block {
				background: transparent;
				padding: 0;
			}

			&:hover {
				opacity: 0.7;
			}
			&::after {
				content: '';
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				top: 50%;
				transform: translateY(-50%);
				right: 20px;
				background: url("../img/icon-arrow-up.svg") no-repeat center / contain;
				transition: transform 0.3s;
			}

			&:hover {
				opacity: 0.8;
			}
		}
		&__content {
			padding: 20px;
			@include sm-block {
				padding: 0;
				padding-top: 10px;
			}

			p {
				font-size: 20px;
				line-height: 28px;
				color: #313243;
				margin: 0;
				@include sm-block {
					font-size: 18px;
				}

				+ p {
					margin-top: 15px;
				}
			}

		}
	}
}