.news {
	background: #EBEFF4;
	padding-top: 44px;
	padding-bottom: 72px;
	@include md-block {
		padding-top: 25px;
	}
	@include sm-block {
		padding-bottom: 20px;
	}
	@include xs-block {
		padding-top: 12px;
	}

	&__row {
		display: flex;
	}

	&__left {
		flex: 1 1 auto;
		min-width: 0;
	}

	&__right{
		flex: 0 0 240px;
		margin-left: 16px;
		@include md-block {
			display: none;
		}
	}
}