.numbered-list {
	padding: 0;
	list-style: none;
	margin: 24px 0;
	&__item {

		font-size: 16px;
		line-height: 24px;
		color: #313243;
		display: flex;


		&:not(:last-of-type) {
			margin-bottom: 12px;
		}

		span {
			width: 40px;
			height: 40px;
			line-height: 40px;
			font-weight: 500;
			font-size: 16px;
			text-align: center;
			color: #3357D7;
			background: #EBEFF4;
			border-radius: 50%;
			display: inline-block;
			margin-right: 8px;
			flex-shrink: 0;
		}

	}
}