.article-tags {
	margin-top: 28px;
	margin-bottom: 36px;
	&__tag {
		border: 1px solid #757A82;
		border-radius: 69px;
		margin-right: 8px;
		padding: 8px 12px;
		font-size: 16px;
		line-height: 24px;
		color: #313243;
		transition: opacity 0.3s;
		text-decoration: none;

		&:hover {
			opacity: 0.7;
		}

	}
}