*, *::before, *::after {
    box-sizing: border-box;
}

body {
    font-family: Roboto, sans-serif;
}
.container {
    max-width: 1180px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    @include sm-block {
        padding-left: 15px;
        padding-right: 15px;
    }

    &--small {
        max-width: 992px;
    }
}

body.news-single-page {
    background-color: #fff;
    .nav {
        margin-bottom: 44px;
    }
    .header {
        @include to(1020px) {
            border-bottom: 1px solid #c9d0db;
            margin-bottom: 20px;
        }
    }
}

html, body {
    overflow-x: hidden;
}

.ad-center {
    text-align: center;
    margin-top: 24px;
    width: 100%;

    &--mb {
        margin-bottom: 24px;
    }
}

img {
    max-width: 100%;
}

.aside-ad {
    margin: 20px 0;
    img {
        width: 100%;
    }

    @include md-block {
        display: none;
    }
}