.author-info {

	padding-bottom: 32px;
	border-bottom: 1px solid rgba(153, 163, 180, 0.4);
	margin-bottom: 32px;

	@include sm-block {
		margin-bottom: 20px;
		padding-bottom: 20px;
	}

	&__row {
		display: flex;
		margin-left: -15px;
		margin-right: -15px;
		@include sm-block {
			flex-direction: column;
			margin-left: 0;
			margin-right: 0;
		}
	}

	&__left, &__right {
		margin-left: 15px;
		margin-right: 15px;
		width: calc(50% - 30px);
		@include sm-block {
			margin-left: 0;
			margin-right: 0;
			width: 100%;
		}
	}

	&__left {
		display: flex;
		flex-direction: column;
		align-items: center;
		@include sm-block {
			margin-bottom: 20px;
		}
	}
	&__right {

	}

	&__img {
		display: block;
		margin-left: auto;
		margin-right: auto;
		border-radius: 50%;
		margin-bottom: 12px;
		border: 3px solid  #45C57B;
	}

	&__name {
		font-weight: 500;
		font-size: 24px;
		line-height: 32px;
		text-align: center;
		color: #313243;
		margin: 0 0 4px 0;
	}
	&__pos {
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #757A82;
	}

	&__about {
		&-label {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 8px;
		}
		&-text {
			margin: 0 0 12px 0;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
		}

	}

	&__rating {
		font-size: 16px;
		line-height: 24px;
		color: #313243;
		display: flex;
		align-items: center;
		&-icon {
			width: 32px;
			height: 32px;
			background: rgba(69, 197, 123, 0.1);
			border-radius: 4px;
			margin-right: 8px;
			display: inline-block;
			position: relative;

			&::after {
				content: "";
				display: block;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				width: 24px;
				height: 24px;
				background: url("../img/icon-green-star.svg") no-repeat center / contain;
			}
		}
		&-label {
			margin-right: 8px;
		}
		&-value {
			color: #45C57B;
		}
	}
}