.authors {
	background: #EBEFF4;
	padding-top: 44px;
	padding-bottom: 72px;

	@include sm-block {
		padding-top: 25px;
		padding-bottom: 60px;
	}

	@include xs-block {
		padding-top: 12px;
		padding-bottom: 44px;
	}


	&__row {
		display: flex;

	align-items: flex-start;
	}

	&__left {
		flex: 1 1 auto;
	}
	&__right {
		flex: 0 0 240px;
		margin-left: 16px;
		@include md-block {
			display: none;
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-left: -6px;
		margin-right: -6px;
		margin-bottom: -12px;

		.ad-block {
			margin-bottom: 20px;
			margin-top: 20px;
			width: calc(100% - 12px);
			margin-left: 6px;
			margin-right: 6px;
			text-align: center;

			img {
			//	max-width: 100%;

			}
		}
	}
}