.calendar {
  border-radius: 12px;
  overflow: hidden;
  margin-bottom: 20px;
}
.calendar__head {
  padding: 16px 16px 12px 16px;
  background: #fff;
  border-radius: 12px 12px 0 0;
  position: relative;
  cursor: pointer;
}
.calendar__head.closed::after {
  transform: translateY(-50%) rotate(-180deg);
}
.calendar__head::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  width: 24px;
  height: 24px;
  background: url("../img/arrow-up.svg") no-repeat center/contain;
  transition: all 0.3s;
}
.calendar__head-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #313243;
}
.calendar__input {
  background: #EBEFF4;
  border-radius: 8px;
  padding: 8px;
  display: block;
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #141414;
  margin-bottom: 12px;
  border: 1px solid transparent;
  transition: all 0.3s;
}
.calendar__input::placeholder {
  color: #757A82;
}
.calendar__input:focus {
  outline: none;
  border-color: #141414;
}
.calendar__content {
  border-top: 1px solid #C9D0DB;
  background: #fff;
  padding: 12px;
  border-radius: 0 0 12px 12px;
}
.calendar__content .air-datepicker.-inline- {
  margin-left: auto;
  margin-right: auto;
  border: none;
}
.calendar__button {
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #313243;
  border: 1px solid #757A82;
  border-radius: 76px;
  margin-top: 12px;
  background: transparent;
  cursor: pointer;
  display: inline-block;
  padding: 10px 20px;
  transition: all 0.3s;
}
.calendar__button:hover {
  background: #313243;
  border-color: #313243;
  color: #ffffff;
}

.city {
  border-radius: 12px;
  overflow: hidden;
}
.city__head {
  padding: 16px 16px 12px 16px;
  background: #fff;
  border-radius: 12px 12px 0 0;
  position: relative;
  cursor: pointer;
}
.city__head.closed::after {
  transform: translateY(-50%) rotate(-180deg);
}
.city__head::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 8px;
  width: 24px;
  height: 24px;
  background: url("../img/arrow-up.svg") no-repeat center/contain;
  transition: all 0.3s;
}
.city__head-text {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #313243;
}
.city__content {
  border-top: 1px solid #C9D0DB;
  background: #fff;
  padding: 12px;
  border-radius: 0 0 12px 12px;
}
.city__radio {
  display: flex;
  align-items: flex-start;
  position: relative;
  cursor: pointer;
}
.city__radio:not(:last-of-type) {
  margin-bottom: 12px;
}
.city__radio input {
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
}
.city__radio input:checked + .city__radio-indicator {
  border-color: #FFC036;
}
.city__radio input:checked + .city__radio-indicator::before {
  opacity: 1;
}
.city__radio-indicator {
  margin-right: 8px;
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  border: 1px solid #C9D0DB;
  border-radius: 50%;
  position: relative;
  transition: all 0.3s;
}
.city__radio-indicator::before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #FFC036;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.city__radio-label {
  font-size: 16px;
  line-height: 24px;
  color: #313243;
}

