.news-single {
	padding-bottom: 72px;

	&__aside-ad {
		width: 100%;
	}

	&__row {
		display: flex;
	}

	&__left {
		flex: 1 1 auto;
		min-width: 0;
	}

	&__right {
		flex: 0 0 240px;
		margin-left: 16px;
		@include md-block {
			display: none;
		}
	}

	&__title {
		font-family: 'Lora';
		font-size: 40px;
		line-height: 52px;
		color: #313243;
		margin: 0 0 12px 0;
		@include md-block {
			font-size: 34px;
			line-height: 40px;
		}
		@include sm-block {
			font-size: 30px;
			line-height: 36px;
		}

		@include xs-block {
			font-size: 26px;
			line-height: 32px;
		}

		&--article {
			font-family: Roboto, sans-serif;
		}
	}

	&__desc {
		font-weight: 400;
		font-size: 24px;
		line-height: 32px;
		color: #313243;
		margin: 0 0 12px 0;
		@include sm-block {
			font-size: 22px;
			line-height: 28px;
		}
	}

	&__date {
		font-weight: 400;
		font-size: 20px;
		line-height: 28px;
		color: #757A82;
		position: relative;
		padding-left: 32px;
		margin-bottom: 16px;
		@include sm-block {
			display: none;
		}

		&::before {
			content: '';
			display: block;
			position: absolute;
			width: 24px;
			height: 24px;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background: url("../img/icon-clock.svg") no-repeat center / contain;
		}
	}

	.news-single {
		&__img {
			//max-width: 100%;
			display: block;
			margin: 0 0 12px;
			border-radius: 16px;

			&-desc {
				font-weight: 400;
				font-size: 12px;
				line-height: 16px;
				color: #757A82;
				margin: 0 0 20px;
				@include xs-block {
					text-align: center;
				}
			}
		}
	}

	&__contents {
		max-width: 393px;
		border: 1px solid #C9D0DB;
		border-radius: 12px;
		margin-top: 24px;
		padding: 16px;
		padding-bottom: 0;
		margin-bottom: 16px;

		&.closed {
			.news-single__contents-head {
				&::after {
					transform: translateY(-50%) rotate(-180deg);
				}
			}
		}

		&-body {
			display: flex;
			flex-direction: column;
			padding-bottom: 16px;
		}

		&-link {

			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}

		&-head {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			padding-right: 50px;
			position: relative;
			display: block;
			padding-bottom: 16px;
			cursor: pointer;
			transition: opacity 0.3s;

			&:hover {
				opacity: 0.8;
			}

			&::after {
				content: '';
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				right: 0;
				top: calc(50% - 8px);
				transform: translateY(-50%);
				background: url("../img/icon-arrow-up.svg");
				transition: all 0.3s;
			}
		}
	}

	&__meta {
		display: none;
		@include sm-block {
			display: block;
			font-weight: 400;
			font-size: 12px;
			line-height: 16px;
			color: #757A82;
			margin-bottom: 16px;
		}
	}

	&__other-articles {
		margin-bottom: 20px;

		&-label {
			display: inline-block;
			margin-bottom: 12px;
			background: #45C57B;
			border-radius: 31px;
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			text-align: center;
			color: #FFFFFF;
			padding: 4px 8px;

			&-wrapper {
				text-align: center;
			}
		}

		&-list {
			margin: 0;
			padding: 0;
			list-style: none;

		}

		&-item {
			display: flex;
			align-items: center;

			&:not(:last-of-type) {
				margin-bottom: 12px;
			}
		}

		&-number {
			font-weight: 500;
			font-size: 16px;
			text-align: center;
			color: #45C57B;
			width: 40px;
			height: 40px;
			background: #EBEFF4;
			line-height: 40px;
			margin-right: 8px;
			flex-shrink: 0;
			display: inline-block;
			border-radius: 50%;
		}

		&-link {
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			text-decoration: none;
			transition: opacity 0.3s;

			&:hover {
				opacity: 0.8;
			}
		}

		&-more {
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			text-decoration: none;
			transition: opacity 0.3s;

			&:hover {
				opacity: 0.8;
			}

			&-wrapper {
				text-align: center;
				margin-top: 12px;
			}
		}
	}

	&__materials {
		margin-top: 20px;

		&-list {
			display: flex;
			flex-direction: column;
		}

		&-item {
			transition: opacity 0.3s;
			display: flex;
			align-items: center;
			text-align: left;
			text-decoration: none;
			padding-top: 8px;
			padding-bottom: 8px;

			&:not(:last-of-type) {
				border-bottom: 1px solid #C9D0DB;
			}

			&:hover {
				opacity: 0.8;
			}
		}

		&-img {
			border-radius: 50%;
			margin-right: 8px;
			flex-shrink: 0;
		}

		&-title {
			font-size: 16px;
			line-height: 24px;
			color: #313243;
		}
	}


}