.author-block {
	width: calc(33.33% - 12px);
	margin-left: 6px;
	margin-right: 6px;
	background: #FFFFFF;
	border-radius: 12px;
	padding: 12px;
	margin-bottom: 12px;
	position: relative;
	overflow: hidden;
	transition: opacity 0.3s;

	@include lg-block {
		width: calc(50% - 12px);
	}
	@include xs-block {
		width: calc(100% - 12px);
	}

	&:hover {
		opacity: 0.8;
	}

	&__top {
		display: flex;
		align-items: center;
	}

	&__img {
		flex-shrink: 0;
		margin-right: 12px;
	}

	&__info {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
	}

	&__name {
		font-size: 16px;
		line-height: 20px;
		color: #313243;
		margin-bottom: 2px;
	}

	&__position {
		font-size: 14px;
		line-height: 20px;
		color: #757A82;
	}

	&__bottom {
		display: flex;
		margin-top: 14px;
		margin-bottom: -5px;
		margin-left: -10px;
		margin-right: -10px;
		flex-wrap: wrap;
	}

	&__stat {
		font-weight: 400;
		font-size: 14px;
		line-height: 20px;
		margin-bottom: 5px;
		margin-left: 10px;
		margin-right: 10px;
		.black {
			margin-right: 4px;
		}
		.green {
			font-weight: 500;
			color: #45C57B;
		}
	}

	&__link {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		display: block;
		z-index: 2;
	}
}