.attraction-tags {
	margin-bottom: 10px;
	&__tag {
		display: inline-block;
		margin-bottom: 10px;
		margin-right: 12px;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #313243;
		padding: 8px 10px;
		border: 1px solid #E20E17;
		box-sizing: border-box;
		border-radius: 30px;
		text-decoration: none;
		transition: all 0.3s;

		&:hover {
			color: #ffffff;
			background: #e20e17;
		}
	}
}