.aside-poster {
	margin-top: 20px;

	&__large-card {
		border: 1px solid #C9D0DB;
		box-sizing: border-box;
		border-radius: 16px;
		overflow: hidden;
		margin-bottom: 4px;

		&-head {
			font-family: 'Lora', sans-serif;
			font-style: normal;
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #FFFFFF;
			padding: 10px 12px;
			background: #FFC036;
			margin: 0;
		}

		&-img {
			width: 100%;
			display: block;
		}

		&-content {
			padding: 12px;
			background: #ffffff;

		}
		&-category {
			font-size: 14px;
			line-height: 20px;
			color: #757A82;
			margin: 0 0 4px 0;
		}
		&-title {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 14px 0;
			text-decoration: none;
			display: block;

			&:hover {
				text-decoration: underline;
			}
		}
		&-bottom {
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
		}
		&-location {
			font-size: 14px;
			line-height: 20px;
			color: #313243;
			padding-left: 32px;
			position: relative;
			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				width: 24px;
				height: 24px;
				background: url("../img/icon-location-yellow.svg") no-repeat center / contain;
			}
		}
		&-time {
			padding-left: 5px;
			flex-shrink: 0;

			.yellow {
				font-weight: 500;
				font-size: 14px;
				line-height: 20px;
				text-align: right;
				color: #FFC036;
			}
		}
	}

	&__small-card {
		min-height: 116px;
		border: 1px solid #C9D0DB;
		border-radius: 12px;
		padding: 12px;

		&:not(:last-of-type) {
			margin-bottom: 4px;
		}
		&-row {
			display: flex;
			align-items: flex-start;
		}
		&-img {
			flex-shrink: 0;
			margin-right: 8px;
		}
		&-content {
			display: flex;
			flex-direction: column;
			align-items: flex-end;
			align-self: stretch;
			flex: 1 1 auto;
		}
		&-title {
			align-self: flex-start;
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			margin: 0 0 2px;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}

		}
		&-location {
			margin: 0 0 2px;
			font-size: 14px;
			line-height: 20px;
			color: #313243;
			position: relative;
			padding-left: 32px;
			margin-top: auto;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				width: 24px;
				height: 24px;
				background: url("../img/icon-location-yellow.svg") no-repeat center / contain;
			}
		}
		&-time {
			margin: 0;
			font-weight: 500;
			font-size: 14px;
			line-height: 20px;
			text-align: right;
			color: #313243;

			.yellow {
				color: #FFC036;
			}
		}
	}
}