.alert-block {
	padding: 20px 20px 20px 68px;
	position: relative;
	margin: 24px 0;
	background: rgba(226, 14, 23, 0.2);
	border-radius: 16px;
	max-width: 672px;
	@include sm-block {
		padding: 12px 12px 12px 52px;
	}
	&::before {
		content: '';
		display: block;
		position: absolute;
		width: 36px;
		height: 36px;
		top: 20px;
		left: 20px;
		background: url(../img/icon-alert.svg) no-repeat center / contain;
		@include sm-block {
			width: 24px;
			height: 24px;
		}
	}
	p {
		font-weight: 400;
		font-size: 20px;
		line-height: 28px;
		margin: 0;
		color: #313243;
		@include sm-block {
			font-size: 18px;
		}
	}
}