.mob-cats {
	overflow-x: auto;
	margin-bottom: 20px;
	display: none;
	@include md-block {
		display: block;
		margin-bottom: 10px;
		padding-bottom: 10px;
	}
	&__list {
		display: flex;
		flex-wrap: nowrap;
	}

	&__item {
		font-size: 16px;
		line-height: 24px;
		color: #313243;
		border: 1px solid #757A82;
		border-radius: 69px;
		text-decoration: none;
		transition: opacity 0.3s;
		display: flex;
		align-items: center;
		padding: 7px 12px;
		white-space: nowrap;

		&:hover {
			opacity: 0.8;
		}

		svg {
			margin-right: 4px;
			fill: #313243;
		}

		&:not(:last-of-type) {
			margin-right: 12px;
		}

	}

	&.blue {
		.mob-cats__item.active {
			background: #3357D7;
			color: #ffffff;
			border-color: #3357D7;
			svg {
				fill: #fff;
			}
		}
	}
	&.green {
		.mob-cats__item.active {
			background: #45C57B;
			color: #ffffff;
			border-color: #45C57B;
			svg {
				fill: #fff;
			}
		}
	}
	&.light-blue {
		.mob-cats__item.active {
			background: #45D0FC;
			color: #ffffff;
			border-color: #45D0FC;
			svg {
				fill: #fff;
			}
		}
	}
}