.header {
	padding: 21px 0;
	background: #FFFFFF;
	@include xs-block {
		padding: 14px 0;
	}

	&__row {
		display: flex;
		align-items: center;
	}

	&__logo-wrapper {
		margin-right: 55px;
		transition: opacity 0.3s;

		@include to(1200px) {
			margin-right: 30px;
		}

		&:hover {
			opacity: 0.7;
		}
	}

	&__logo-img {
		&--desktop {
			@include xs-block {
				display: none;
			}
		}

		&--mob {
			display: none;
			@include xs-block {
				display: block;
			}
		}
	}

	&__date {
		display: flex;
		flex-direction: column;
		align-items: flex-start;

		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		margin-right: 55px;
		flex-shrink: 0;

		&-label {

		}

		&-value {
			color: #313243;
		}

		@include to(1000px) {
			display: none;
		}
	}

	&__brief {
		display: flex;
		align-items: center;
		margin-right: 32px;

		@include to(1200px) {
			margin-right: 20px;
		}

		@include to(1000px) {
			display: none;
		}

		&-col {
			padding-right: 20px;
			margin-right: 20px;
			position: relative;
			display: flex;
			align-items: center;

			@include to(1200px) {
				margin-right: 15px;
			}

			&::after {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 0;
				width: 1px;
				height: 30px;
				background: #757A82;
				border-radius: 5px;
			}

			&:last-of-type {
				padding-right: 0;
				margin-right: 0;

				&::after {
					display: none;
				}
			}
		}

		&-icon {
			margin-right: 8px;
		}

		&-label {
			font-size: 14px;
			line-height: 20px;
			text-transform: uppercase;
			color: #757A82;
		}
	}

	&__search {
		&-wrapper {
			margin-left: auto;

			@include xs-block {
				position: absolute;
				left: 0;
				width: 100%;
				display: none;
				z-index: 3;
				top: 52px;
				background: red;

				&.active {
					display: block;
				}
			}
		}

		&-form {
			position: relative;

		}

		&-input {
			border: 1px solid rgba(117, 122, 130, 0.4);
			box-sizing: border-box;
			border-radius: 46px;
			color: #313243;
			font-size: 16px;
			padding: 10px 50px 10px 15px;
			transition: border 0.3s;
			width: 200px;
			@include xs-block {
				width: 100%;
				border-radius: 0;
			}


			&:focus {
				outline: none;
				border: 1px solid rgba(117, 122, 130, 0.8);
			}
		}

		&-btn {
			position: absolute;
			background: url("../img/icon-search.svg") no-repeat center / contain;
			width: 24px;
			height: 24px;
			top: 50%;
			transform: translateY(-50%);
			right: 12px;
			border: none;
			cursor: pointer;
			transition: opacity 0.3s;
			padding: 0;

			&:hover {
				opacity: 0.7;
			}

			&:focus {
				outline: none;
			}
		}
	}

	&__account {
		&-links {
			position: relative;
			padding-left: 36px;
			margin-left: 32px;
			@include to(1150px) {
				display: none;
			}

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				width: 24px;
				height: 24px;
				background: url("../img/icon-login.svg") no-repeat center / contain;
			}

		}

		&-link {
			font-size: 14px;
			line-height: 20px;
			color: #757A82;
			text-decoration: none;
			transition: color 0.3s;

			&:hover {
				color: #444549;
			}


			&:first-of-type {
				margin-right: 24px;
				position: relative;

				&::before {
					content: "";
					display: block;
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					right: -12px;
					width: 1px;
					height: 20px;
					background: #757A82;
					opacity: 0.4;
				}
			}

		}
	}

	&__search-open-btn {
		display: none;
		@include xs-block {
			display: block;
			width: 24px;
			height: 24px;
			background: url("../img/icon-search.svg") no-repeat center / contain;
			flex-shrink: 0;
			padding: 0;
			border: none;
			margin-left: auto;
			&:focus {
				outline: none;
			}

			&.active {
				background-image: url("../img/icon-close.svg");
			}
		}
	}

	&__mob-account {
		width: 24px;
		height: 24px;
		background: url("../img/icon-user.svg") no-repeat center / contain;
		display: none;
		flex-shrink: 0;
		@include to(1150px) {
			display: block;
			margin-left: 36px;
			position: relative;
			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: -18px;
				width: 1px;
				height: 38px;
				background: #c9d0db;
			}
		}
	}
}