.attraction-contacts {
	background: #FFFFFF;
	border: 1px solid #C9D0DB;
	box-sizing: border-box;
	border-radius: 12px;
	padding: 12px;
	margin-bottom: 20px;

	&__row {
		display: flex;
		align-items: flex-start;
		font-size: 16px;
		line-height: 24px;
	}

	&__label {
		color: #313243;
		margin-right: 8px;
		font-weight: 500;
		flex-shrink: 0;
	}

	&__value {
		color: #757A82;
	}

	a.attraction-contacts__value {
		text-decoration: underline;

		&:hover {
			text-decoration: none;
		}
	}

	&__btn {
		display: inline-block;
		width: 100%;
		border-radius: 50px;
		background: #E20E17;
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #FFFFFF;
		margin-top: 16px;
		padding: 11px 20px 9px 20px;
		text-decoration: none;
		transition: opacity 0.3s;

		&:hover {
			opacity: 0.7;
		}
	}
}