.now-reading {

	background: #FFFFFF;
	border: 1px solid #C9D0DB;
	border-radius: 16px;
	margin-top: 20px;
	overflow: hidden;

	&__head {
		background: #E20E17;
		padding: 10px 12px;
		font-family: 'Lora';
		font-style: normal;
		font-weight: 500;
		font-size: 20px;
		line-height: 28px;
		color: #FFFFFF;
		margin: 0;
	}
	&__list {
		padding: 12px 12px 16px 12px;
	}
	&__item {
		display: flex;
		align-items: flex-start;
		&:not(:last-of-type) {
			margin-bottom: 12px;
		}
		&-number {
			flex-shrink: 0;
			background: #EBEFF4;
			border-radius: 50%;
			width: 40px;
			height: 40px;
			text-align: center;
			line-height: 40px;
			margin-right: 8px;

			font-weight: 500;
			font-size: 16px;
			color: #E20E17;
		}

		&-title {
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}