.attraction-content {
	&__title {
		font-weight: 700;
		font-size: 40px;
		line-height: 52px;
		color: #313243;
		margin: 0 0 12px 0;

		@include sm-block {
			font-size: 32px;
			line-height: 38px;
		}

		@include xs-block {
			font-size: 26px;
			line-height: 32px;
		}
	}

	&__img {

		&-desc {
			font-size: 12px;
			line-height: 16px;
			color: #757A82;
		}

	}

	&__large-text {
		font-weight: 400;
		font-size: 24px;
		line-height: 32px;
		color: #313243;
		margin: 0 0 16px 0;
		@include sm-block {
			font-size: 22px;
			line-height: 28px;
		}
	}

	&__text {
		font-size: 18px;
		line-height: 28px;
		color: #313243;
		margin: 0 0 16px 0;
	}

	&__gallery {
		margin-top: 24px;

		&-title {
			margin: 0 0 16px 0;
			font-weight: 600;
			font-size: 32px;
			line-height: 40px;
			color: #313243;
		}
	}

	h2 {
		ont-weight: 500;
		font-size: 24px;
		line-height: 32px;
		color: #313243;
		margin: 0 0 16px 0;
	}

}