.cats {
	margin-top: 16px;
	margin-bottom: 16px;

	@include lg-block {
		.container {
			padding-left: 10px;
			padding-right: 10px;
		}
	}
	@include md-block {
		display: none;
	}

	&__row {
		display: flex;
	}

	&__item {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-decoration: none;
		transition: opacity 0.3s;
		flex: 1 1 auto;

		&:not(:last-of-type) {
			position: relative;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 0;
				width: 1px;
				height: 36px;
				background: #c9d0db;
			}
		}


		&:hover {
			opacity: 0.7;
		}

		&-img-wrapper {
			width: 52px;
			height: 52px;
			display: flex;
			align-items: center;
			justify-content: center;
			background: rgba(51, 87, 215, 0.1);
			margin-bottom: 5px;
			border-radius: 4px;
		}

		&-img {
			display: block;
		}

		&-text {
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			text-align: center;
			color: #313243;

		}

	}

	&--green {
		.cats__item-img-wrapper {
			background: rgba(69, 197, 123, 0.1);
		}
	}
	&--light-blue {
		.cats__item-img-wrapper {
			background: rgba(69, 208, 252, 0.1);
		}
	}
	&--yellow {
		.cats__item-img-wrapper {
			background: rgba(255, 192, 54, 0.1);
		}
	}
}