.gallery {
	position: relative;
	.swiper-wrapper {
		height: auto;
	}
	&__arrow {
		width: 44px;
		height: 44px;
		border-radius: 50%;
		border: none;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		z-index: 3;
		cursor: pointer;
		transition: opacity 0.3s;

		&:hover {
			opacity: 0.8;
		}

		&:focus {
			outline: none;
		}

		&.swiper-button-disabled {
			opacity: 0;
		}

		&--left {
			left: 0;
			background: url("../img/icon-arrow-left.svg") no-repeat center / 24px, #FFF;
		}

		&--right {
			right: 0;
			background: url("../img/icon-arrow-right.svg") no-repeat center / 24px, #FFF;
		}


	}
}