.nav {
	background: #313243;
	@include to(1020px) {
		display: none;
	}

	.container {
		@include to(1100px) {
			padding-left: 0;
			padding-right: 0;
		}
	}

	&__list {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 0;
		margin: 0;
		list-style: none;
	}

	&__item {
	}

	&__link {
		font-weight: 400;
		font-size: 16px;
		color: #ffffff;
		text-decoration: none;
		padding: 15px;
		display: inline-block;
		transition: opacity 0.3s;

		&.active {
		}

		&:hover {
			background: #3357D7;
		}

		&.blue {
			&.active, &:hover {
				background: #3357D7;
			}
		}
		&.light-blue {
			&.active, &:hover {
				background: #45D0FC;
			}
		}

		&.green {
			&.active, &:hover {
				background: #45C57B;
			}
		}
	}
}