.breadcrumbs {
	margin-bottom: 16px;
	@include sm-block {
		//display: none;
	}

	&__link, &__current, &__sep {
		text-decoration: none;
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		@include sm-block {
			font-size: 14px;
			line-height: 20px;
		}
	}

	&__sep {
		margin: 0 0.3em;
	}

	&__link {
		&:hover {
			text-decoration: underline;
		}
	}

	&--mt {
		margin-top: 44px;
		@include xs-block {
			margin-top: 20px;
		}
	}

	&--mb {
		margin-bottom: 16px;
	}
}