.article-brief {
	display: flex;
	align-items: center;
	padding-bottom: 20px;
	border-bottom: 1px solid #c9d0db;
	margin-bottom: 36px;

	@include xs-block {
		flex-wrap: wrap;
	}

	&__author-img {
		margin-right: 8px;
		flex-shrink: 0;
	}

	&__author {
		&-info {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}

		&-name {
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			margin-bottom: 2px;
		}
		&-status {
			background: #757A82;
			border-radius: 40px;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #FFFFFF;
			padding: 4px 6px;

			&--transparent {
				padding: 0;
				color: #757a82;
				background: transparent;
			}
		}
	}

	&__right {
		margin-left: auto;
		display: flex;
		align-items: center;
		@include xs-block {
			flex: 0 0 100%;
			margin-left: 0;
			margin-top: 24px;
		}
	}

	&__stat {
		display: flex;
		align-items: center;
		margin-right: 62px;
		@include xs-block {
			margin-right: auto;
		}
		&-block {
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			padding-left: 34px;
			position: relative;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				width: 24px;
				height: 24px;
				background: no-repeat center / contain;
			}

			&--likes {
				margin-right: 30px;

				&::before {
					background-image: url("../img/icon-likes.svg");
				}

			}
			&--dislikes {
				&::before {
					background-image: url("../img/icon-dislikes.svg");
				}
			}
		}
	}

	&__like-btn {
		width: 24px;
		height: 24px;
		background: transparent;
		border: none;
		position: relative;
		cursor: pointer;
		transition: opacity 0.3s;

		&:hover {
			opacity: 0.8;
		}
		
		img {

		}

		&:focus {
			outline: none;
		}
		&-icon {
			transition: all 0.3s;
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%,-50%);

			&--inactive {
				opacity: 1;
				//transform: scale(1) translate(-50%, -50%);
			}
			&--active {
				opacity: 0;
				//transform: scale(0) translate(-50%, -50%);
			}
		}

		&.liked {
			.article-brief__like-btn-icon--active {
				opacity: 1;
				//transform: scale(1) translate(-50%, -50%);
			}
			.article-brief__like-btn-icon--inactive {
				opacity: 0;
				//transform: scale(0) translate(-50%, -50%);
			}
		}
	}

	&__share-link {
		width: 24px;
		height: 24px;
		margin-left: 23px;
		background: url("../img/icon-share.svg") no-repeat center / contain;
		transition: all 0.3s;

		&:hover {
			opacity: 0.7;
		}
		
	}

	&__rating {
		display: flex;
		align-items: center;

		&-label {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin-right: 16px;
		}
		&-stars {
			img {
				cursor: pointer;
			}

		}
	}
}