.main-layout {
	padding-top: 44px;
	padding-bottom: 72px;
	background: #EBEFF4;



	&--white {
		background: #fff;
	}

	@include md-block {
		padding-top: 35px;
	}

	@include sm-block {
		padding-top: 20px;
	}

	@include xs-block {
		padding-top: 12px;
	}
	&--no-pt {
		padding-top: 0;
	}

	&__row {
		display: flex;
		@include md-block {
			flex-direction: column;
		}

		&--mobile-reverse {
			@include md-block {
				flex-direction: column-reverse;
			}

			.main-layout__aside {
				@include md-block {
					margin-bottom: 16px;
				}
			}
		}
	}

	&__content {
		flex: 1 1 auto;
		min-width: 0;
	}

	&__aside {
		flex: 0 0 300px;
		margin-left: 30px;
		min-width: 0;

		@include md-block {
			margin-left: 0;
			flex: unset;
		}

		&--ad {
			img {
				width: 100%;
			}
			@include md-block {
				display: none;
			}
		}
	}
}