.recommendations {
	margin-top: 36px;

	.recommendations {
		&__title {
			margin: 0 0 20px 0;
			font-size: 28px;
			line-height: 36px;
			color: #313243;
			@include xs-block {
				text-align: center;
			}
		}
	}

	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-left: -8px;
		margin-right: -8px;
		margin-bottom: -20px;
		@include xs-block {
			justify-content: center;
		}

	}

	&__block {
		width: calc(33.33% - 16px);
		margin-left: 8px;
		margin-right: 8px;
		margin-bottom: 20px;
		border: 1px solid #757A82;
		box-sizing: border-box;
		border-radius: 16px;
		overflow: hidden;
		min-width: 260px;
	}

	&__img {
		display: block;
		width: 100%;

		&-wrapper {
			display: block;
			width: 100%;
			transition: opacity 0.3s;

			&:hover {
				opacity: 0.7;
			}
		}
	}

	.recommendations {
		&__block-title {
			font-family: Roboto, sans-serif;
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			text-decoration: none;
			transition: opacity 0.3s;

			&:hover {
				opacity: 0.8;
			}
		}
	}

	&__content {
		padding: 8px;

	}

	&__bottom {
		display: flex;
		margin-top: 8px;
	}

	&__author {
		&-img {
			flex-shrink: 0;
			margin-right: 8px;
			border-radius: 50%;
		}

		&-name {
			font-weight: 400;
			font-size: 14px;
			line-height: 20px;
			color: #313243;
		}
	}

	&__location {
		margin: 0;
		padding-left: 32px;
		position: relative;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 24px;
			height: 24px;
			background: url("../img/icon-location-light-blue.svg") no-repeat center / contain;
		}
	}
}