.partners-news {
	margin-bottom: 40px;
	margin-top: 40px;
	@include sm-block {
		display: none;
	}

	.partners-news {
		&__title {
			font-size: 32px;
			line-height: 40px;
			color: #313243;
			margin-bottom: 28px;
			margin-top: 0;
			font-family: Lora, sans-serif;
		}
		
		&__slider-wrapper {
			position: relative;
			.swiper-button-prev {
				left: 0;
				&::after {
					background: url("../img/icon-arrow-left.svg");
				}
			}
			.swiper-button-next {
				right: 0;
				&::after {
					background: url("../img/icon-arrow-right.svg");
				}
			}
			.swiper-button-prev, .swiper-button-next {
				width: 44px;
				height: 44px;
				background: #FFFFFF;
				border-radius: 50%;
				top: 50%;
				margin-top: 0;
				transform: translateY(-50%);
				transition: opacity 0.3s;

				&::after {
					width: 24px;
					height: 24px;
					content: '';
					font-size: 0;
				}

				&.swiper-button-disabled {
					opacity: 0;
					pointer-events: none;
				}

				&:hover {
					opacity: 0.7;
					cursor: pointer;
				}
			}

		}

		&__slide {
			background: no-repeat bottom 0 left 50% / cover;
			padding: 12px;

			text-decoration: none;
			height: 300px;
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			transition: opacity 0.3s;
			border-radius: 16px;

			&:hover {
				opacity: 0.8;
			}

			&-text {
				font-weight: 400;
				font-size: 16px;
				line-height: 24px;
				color: #FFFFFF;
				margin: 0;
				//max-width: 100%;
			}
		}
	}


}