.news-content {


	p {
		font-size: 20px;
		line-height: 28px;
		color: #313243;
		margin: 0 0 20px 0;
		@include sm-block {
			font-size: 18px;
			line-height: 28px;
		}
	}

	h2 {
		font-weight: 500;
		font-size: 24px;
		line-height: 32px;
		color: #313243;
		margin: 24px 0 16px 0;
		@include sm-block {
			font-size: 20px;
			line-height: 28px;
			margin-top: 20px;
			margin-bottom: 12px;
		}

	}
}