.attraction-card {
	width: calc(33.33% - 16px);
	margin-left: 8px;
	margin-right: 8px;
	margin-bottom: 17px;

	border-radius: 16px;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	align-items: stretch;
	@include to(1180px) {
		width: calc(50% - 16px);
	}
	@include md-block {
		width: calc(33.33% - 16px);
	}

	@include sm-block {
		width: calc(50% - 16px);
	}
	@include xs-block {
		width: calc(100% - 16px);
		margin-bottom: 12px;
		max-width: 450px;
		margin-left: auto;
		margin-right: auto;
	}

	&__img-wrapper {

		transition: opacity 0.3s;

		&:hover {
			opacity: 0.7;
		}
	}

	&__img {
		display: block;
		width: 100%;
	}

	&__content {
		padding: 12px;
		background: #ffffff;
		display: flex;
		flex-direction: column;
		flex-grow: 1;
	}

	&__title {
		font-weight: 500;
		font-size: 20px;
		line-height: 28px;
		color: #313243;
		margin: 0 0 8px 0;
		text-decoration: none;

		&:hover {
			text-decoration: underline;
		}
	}

	&__location {
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		position: relative;
		padding-left: 32px;

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 24px;
			height: 24px;
			background: url("../img/icon-location-light-blue.svg") no-repeat center / contain;
		}
	}
}