.info-block {
	max-width: 672px;
	border: 1px solid #C4C4C4;
	border-radius: 16px;
	overflow: hidden;
	margin: 24px 0;
	.info-block {
		&__title {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 8px 0;
			@include sm-block {
				font-size: 18px;
				margin-bottom: 6px;
			}
		}

		&__text {
			margin: 0;
			font-weight: 400;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			@include sm-block {
				font-size: 18px;
			}
		}

		&__img {
			width: 100%;
			display: block;
		}

		&__content {
			padding: 20px 20px 16px 68px;
			position: relative;

			@include sm-block {
				padding: 8px 8px 8px 40px;
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				width: 36px;
				height: 36px;
				top: 20px;
				left: 20px;
				background: url("../img/icon-info.svg") no-repeat center / contain;

				@include sm-block {
					width: 24px;
					height: 24px;
					top: 10px;
					left: 8px;
				}
			}
		}
	}
}