.comment-item {

	&:not(:last-of-type) {
		margin-bottom: 20px;
	}

	&__head {
		display: flex;
		align-items: center;
		margin-bottom: 12px;
	}

	&__img {
		margin-right: 8px;
		flex-shrink: 0;
		border-radius: 50%;

	}

	&__meta {
		display: flex;
		flex-direction: column;
	}

	&__name {
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		color: #313243;
	}

	&__date {
		font-weight: 400;
		font-size: 14px;
		line-height: 20px;
		color: #757A82;
	}

	&__body {


		p {
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			margin: 0;

			@include sm-block {
				font-size: 18px;
				line-height: 28px;
			}

			+ p {
				margin-top: 15px;
			}
		}

		&.hidden {

			.hidden {
				display: block;
			}

			.full {
				display: none;
			}
		}
		&.show {
			.hidden {
				display: none;
			}
			.full {
				display: block;
			}
		}

	}

	&__more-btn {
		font-weight: 400;
		font-size: 14px;
		line-height: 20px;
		text-decoration-line: underline;
		color: #757A82;
		padding: 0;
		background: transparent;
		border: none;
		margin-top: 4px;

		&:focus {
			outline: none;
		}

		&:hover {
			text-decoration: none;
		}
	}
}