.news-item {
	border-radius: 16px;
	overflow: hidden;
	position: relative;
	display: flex;
	@include sm-block {
		background: #FFFFFF;
		box-shadow: 0px 2px 1px rgba(190, 196, 204, 0.36);
		border-radius: 12px;
		max-width: 460px;
		margin-left: auto;
		margin-right: auto;
	}

	&--large {
		@include sm-block {
			flex-direction: column;
		}
		.news-item__img-wrapper {
			min-height: 368px;
			flex: 0 0 50%;
			@include md-block {
				min-height: 310px;
			}

			@include sm-block {
				padding-right: 0;
				padding-top: 0;
				flex: unset;
				min-height: unset;

				img {
					width: 100%;
					height: auto;
					border-radius: 16px 16px 0 0 ;
					display: block;
				}
			}
		}

		.news-item__tag {
			border-radius: 16px 0px;
			right: auto;
			left: 0;
			display: block;
			@include sm-block {
				left: auto;
				right: 0;
				border-radius: 0 16px;
			}
		}

		.news-item__title {
			font-size: 24px;
			line-height: 32px;
			margin-bottom: 12px;
			@include md-block {
				font-size: 22px;
				line-height: 30px;
			}
			@include sm-block {
				font-size: 20px;
				line-height: 30px;
			}
			@include xs-block {
				font-size: 18px;
				line-height: 28px;
			}
		}

		.news-item__content {
			display: flex;
			flex-direction: column;

			.news-item__bottom {
				margin-top: auto;
			}

			.news-item__desc {
				margin-top: 0;
			}
		}
	}

	&:not(:last-of-type) {
		margin-bottom: 16px;
		@include sm-block {
			margin-bottom: 12px;
		}
	}
	&__content {
		flex: 1 1 auto;
	}

	&__img-wrapper {
		position: relative;
		flex: 0 0 280px;

		@include sm-block {
			flex: 0 0 auto;
			padding-top: 8px;
			padding-right: 8px;
			padding-bottom: 8px;
		}
		img {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			width: 100%;
			height: 100%;
			object-fit: cover;
			@include sm-block {
				position: static;
				transform: none;
				width: 96px;
				height: 96px;
				border-radius: 8px;
			}
		}
		transition: opacity 0.3s;

		&:hover {
			opacity: 0.7;
		}
	}

	&__tag {
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #FFFFFF;
		padding: 6px 18px;
		background: #3357D7;
		border-radius: 0 16px 0 16px;
		display: inline-block;
		position: absolute;
		right: 0;
		top: 0;
		text-decoration: none;
		transition: opacity 0.3s;
		z-index: 2;

		@include sm-block {
			display: none;
		}

		&.green {
			background: #45C57B;
		}

		&:hover {
			opacity: 0.7;
		}
	}

	&__content {
		background: #ffffff;
		padding: 16px;
		@include sm-block {
			padding: 8px;
		}
	}

	&__title {
		font-family: 'Lora', sans-serif;
		font-size: 20px;
		line-height: 28px;
		color: #313243;
		margin: 0 0 4px 0;
		text-decoration: none;
		font-weight: 600;

		@include sm-block {
			font-size: 18px;
			line-height: 26px;
			font-weight: 600;
		}

		@include xs-block {
			font-size: 16px;
			line-height: 24px;
		}

		&:hover {
			text-decoration: none;
		}
	}

	&__desc {
		font-size: 16px;
		line-height: 24px;
		color: #313243;
		font-weight: 400;
		@include sm-block {
			display: none;
		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include sm-block {
			display: none;
		}
	}

	&__date {
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		padding-right: 30px;

		@include xs-block {
			font-size: 14px;
			line-height: 20px;
		}
	}

	&__views {
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		padding-left: 32px;
		position: relative;

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 24px;
			height: 24px;
			background: url("../img/icon-views.svg") no-repeat center / contain;
		}
	}

	&__top {
		display: none;
		@include sm-block {
			display: block;
			font-weight: 400;
			font-size: 14px;
			line-height: 20px;
			color: #757A82;
			margin-bottom: 8px;
		}
	}

	&--bordered {
		border: 1px solid rgba(153, 163, 180, 0.4);
	}

	&__author {
		display: flex;
		align-items: center;

		&-img {
			flex-shrink: 0;
			border-radius: 50%;
			margin-right: 8px;
		}

		&-name {
			font-size: 14px;
			line-height: 20px;
			color: #313243;
		}
	}
}