.address-block {
	padding-left: 20px;
	border-left: 1px solid #3357D7;
	margin: 24px 0;

	@include sm-block {
		padding-left: 0;
		padding-top: 12px;
		padding-bottom: 12px;
		border-top: 1px solid #C9D0DB;
		border-bottom: 1px solid #C9D0DB;
		margin: 20px 0;
		border-left: 0;
	}

	.address-block {
		&__title {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 8px 0;
			@include sm-block {
				margin-bottom: 4px;
			}
		}

		&__text {
			font-weight: 400;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 16px 0;
			@include sm-block {
				font-size: 18px;
				margin-bottom: 12px;
			}
		}

		&__address {
			margin: 16px 0 0;
			font-weight: 400;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			padding-left: 32px;
			position: relative;
			@include sm-block {
				font-size: 18px;
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				width: 24px;
				height: 24px;
				background: url("../img/icon-geo.svg") no-repeat center / contain;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
			}
		}
	}
}