.poster {
	&__list {
		display: flex;
		flex-wrap: wrap;
		margin-left: -9px;
		margin-right: -9px;
		margin-bottom: -17px;
		@include xs-block {
			margin-bottom: -12px;
		}
	}

	&__card {
		width: calc(33.33% - 18px);
		margin-left: 9px;
		margin-right: 9px;
		margin-bottom: 17px;
		position: relative;
		border-radius: 16px;
		overflow: hidden;
		height: auto;
		display: flex;
		flex-direction: column;
		align-items: stretch;

		@include to(1200px) {
			width: calc(50% - 18px);
		}
		@include md-block {
			width: calc(33.33% - 18px);
		}
		@include sm-block {
			width: calc(50% - 18px);
		}

		@include xs-block {
			width: calc(100% - 18px);
			margin-bottom: 12px;
		}

		&-meta {
			display: flex;
			flex-direction: column;

		}

		&-row {
			display: flex;
			@include xs-block {
				display: block;
			}
		}

		&-left, &-right {
			width: 50%;
			@include xs-block {
				width: 100%;
			}
		}

		&-left {
			display: block;
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
				display: block;
				border-radius: 0;
			}
		}

		&-right {
			padding: 16px;
			background: #fff;
		}

		&--large {
			width: calc(100% - 18px);

			.poster__card-category {
				left: 0;
				top: 0;
				right: auto;
				border-radius: 16px 0;
			}

		}

		&-category {
			background: #FFC036;
			border-radius: 0px 16px;
			display: inline-block;
			position: absolute;
			right: 0;
			top: 0;
			padding: 6px 18px;
			font-weight: 500;
			font-size: 16px;
			line-height: 24px;
			color: #FFFFFF;
			z-index: 2;
			@include xs-block {
				display: none;
			}
		}

		&-img {
			display: block;
			width: 100%;
			border-radius: 18px 18px 0 0;

			&-wrapper {
				transition: opacity 0.3s;
				display: block;
				@include xs-block {
					display: none;
				}

				&:hover {
					opacity: 0.8;
				}
			}
		}

		&-content {
			padding: 12px;
			background: #ffffff;
			border-radius: 0 0 16px 16px;
			flex-grow: 1;
			&-top {
				@include xs-block {
					display: flex;
					align-items: flex-start;
					justify-content: space-between;
					margin-bottom: 8px;
				}
			}
		}

		&-title {
			font-weight: 500;
			font-size: 20px;
			line-height: 28px;
			color: #313243;
			margin: 0 0 8px 0;
			display: block;
			text-decoration: none;

			&:hover {
				text-decoration: underline;
			}

		}

		&-desc {
			margin: 0 0 16px 0;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			font-weight: 400;
		}

		&-info {
			display: flex;
			flex-wrap: wrap;
			@include xs-block {
				max-width: 210px;
			}
		}

		&-time, &-price, &-location {
			position: relative;
			display: inline-block;
			margin-top: 0;
			padding-left: 32px;
			margin-bottom: 8px;
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #757A82;

			&::before {
				content: "";
				display: block;
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				left: 0;
				background: no-repeat center / contain;
				width: 24px;
				height: 24px;
			}
		}

		&-time {
			margin-right: auto;
			&::before {
				background-image: url("../img/icon-time-yellow.svg");
			}
		}
		&-price {
			&::before {
				background-image: url("../img/icon-price-yellow.svg");
			}
		}
		&-location {
			&::before {
				background-image: url("../img/icon-location-yellow.svg");
			}
		}

		&-mob-img {
			display: none;
			@include xs-block {
				display: block;
				width: 64px;
				height: 64px;
				object-fit: cover;
				object-position: center;
				margin-left: 8px;
				flex-shrink: 0;
				border-radius: 8px;
			}
		}
	}
}