.comments {
	margin-top: 36px;

	@include sm-block {
	}

	&__head {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 22px;

		@include sm-block {
			.comments__new-btn {
				display: none;
			}
			.comments__all-link {
				display: block;
				font-weight: 400;
				font-size: 14px;
				line-height: 20px;
				color: #757A82;
				margin-left: auto;
				padding-left: 15px;
				text-decoration: none;
				&:hover {
					text-decoration: underline;
				}

			}
		}
	}

	.comments {

		&__title {

			margin: 0;
			font-size: 28px;
			line-height: 36px;
			color: #313243;
			font-weight: 500;
			@include sm-block {
				font-size: 24px;
				line-height: 32px;
			}
		}
	}

	&__new-btn {
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #313243;
		margin-left: 30px;
		padding: 8px 10px;
		transition: all 0.3s;

		border: 1px solid #757A82;
		border-radius: 30px;
		text-decoration: none;

		&:hover {
			opacity: 0.7;
		}
	}

	&__more-btn {
		background: #EBEFF4;
		border-radius: 54px;
		font-weight: 500;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #313243;
		padding: 10px 36px;
		transition: opacity 0.3s;
		display: inline-block;
		cursor: pointer;
		margin-top: 24px;
		border: none;

		@include sm-block {
			display: none;
		}

		&:focus {
			outline: none;
		}

		&:hover {
			opacity: 0.7;
		}

	}

	&__mob-add-btn {
		display: none;
		font-weight: 400;
		font-size: 16px;
		line-height: 24px;
		text-align: center;
		color: #313243;
		border: 1px solid #757A82;
		box-sizing: border-box;
		border-radius: 76px;
		text-decoration: none;
		padding: 12px 30px;
		margin-bottom: 24px;
		margin-top: 24px;
		@include sm-block {
			display: block;
		}
	}

	&__all-link {
		display: none;
	}
}