.eating-block {
	border: 1px solid #C4C4C4;
	box-sizing: border-box;
	border-radius: 16px;
	max-width: 672px;
	padding: 24px 20px 20px 20px;
	margin: 24px 0;

	@include sm-block {
		padding: 16px;
	}

	&__title {
		font-weight: 500;
		font-size: 24px;
		line-height: 28px;
		color: #313243;
		margin: 0 0 16px 0;
		padding-left: 48px;
		position: relative;
		@include sm-block {
			font-size: 20px;
			padding-left: 36px;
			margin-bottom: 12px;
		}

		&::before {
			content: '';
			display: block;
			position: absolute;
			width: 36px;
			height: 36px;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			background: url("../img/icon-eating-red.svg") no-repeat center / contain;
			@include sm-block {
				width: 24px;
				height: 24px;
			}
		}
	}

	&__desc {
		font-weight: 400;
		font-size: 20px;
		line-height: 28px;
		color: #313243;
		margin: 0 0 12px 0;
		@include sm-block {
			font-size: 18px;
			margin-bottom: 8px;

		}
	}

	&__bottom {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		margin-bottom: -10px;
	}

	&__time, &__location, &__price {
		font-size: 16px;
		line-height: 24px;
		color: #757A82;
		margin-right: 16px;
		padding-left: 32px;
		position: relative;
		margin-bottom: 10px;
		@include sm-block {
			font-size: 14px;
			margin-right: 12px;

		}
		&::before {
			content: '';
			display: block;
			position: absolute;
			width: 24px;
			height: 24px;
			background: no-repeat center / contain;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
		}
	}

	&__time {
		&::before {
			background-image: url("../img/icon-clock-red.svg");
		}
	}

	&__location {
		&::before {
			background-image: url("../img/icon-location-red.svg");
		}
	}

	&__price {
		&::before {
			background-image: url("../img/icon-price-red.svg");
		}
	}
}