.article-head {
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding-bottom: 16px;
	margin-bottom: 16px;
	border-bottom: 1px solid #C9D0DB;

	@include xs-block {
		flex-direction: column;
		align-items: flex-start;
	}
	&__left {
		display: flex;
		align-items: center;
		@include xs-block {
		}
	}

	&__right {
		display: flex;
		align-items: center;
		@include xs-block {
			margin-top: 15px;
		}
	}

	&__img {
		margin-right: 8px;
		border-radius: 50%;
		border: 2px solid #45C57B;
	}

	&__author {
		&-info {
			font-weight: 400;
			font-size: 16px;
			line-height: 24px;
			color: #313243;
			span {
				display: block;
			}
		}
	}

	&__date {
		margin-right: 28px;
		margin-left: 30px;
		@include xs-block {
			margin-left: 0;
		}
	}
	&__views {
		padding-left: 32px;
		position: relative;

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 24px;
			height: 24px;
			background: url("../img/icon-green-eye.svg") no-repeat center / contain;
		}

	}

	&__date, &__views {
		font-size: 20px;
		line-height: 28px;
		color: #757A82;

		@include sm-block {
			font-size: 16px;
			line-height: 22px;
		}
	}
}