.poster-meta {
	margin-bottom: 12px;
	display: flex;
	flex-wrap: wrap;

	&__date, &__price {
		font-weight: 400;
		font-size: 20px;
		line-height: 28px;
		color: #757A82;
		padding-left: 32px;
		position: relative;
		margin-right: 28px;

		@include xs-block {
			font-size: 16px;
			line-height: 24px;
		}

		&::before {
			content: "";
			display: block;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			width: 24px;
			height: 24px;
			background: url("../img/icon-time-yellow.svg") no-repeat center / contain;
		}
	}

	&__date {

	}

	&__price {
		&::before {
			background: url("../img/icon-price-yellow.svg") no-repeat center / contain;
		}
	}
}