.author-page {
	padding-bottom: 72px;

	@include sm-block {
		padding-bottom: 60px;
	}

	@include xs-block {
		padding-bottom: 44px;
	}
	&__row {
		display: flex;

		align-items: flex-start;
	}

	&__left {
		flex: 1 1 auto;
	}
	&__right {
		flex: 0 0 240px;
		margin-left: 16px;
		@include md-block {
			display: none;
		}
	}
}
